html {
  scroll-snap-type: y mandatory;
}

.App {
  display: grid;
  place-items: center;
  background-color: #121212;
  height: 100vh;
}

.app__videos {
  height: 100vh;
  max-height: 800px;
  width: 80%;
  max-width: 500px;
  border-radius: 30px;
  background-color: aliceblue;
  overflow: scroll;
  scrollbar-width: none;
  scroll-snap-type: y mandatory;
  position: relative;
}

/* Esconde a scroll bar no chrome, safari e opera */

.app__videos::-webkit-scrollbar {
  display: none;
}

/* Esconder a scroll bar no internet explorer, edge e firefox */

.app__videos {
  -ms-overflow-style: none;
  scrollbar-width: none;
}